.projects-sub-page {
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 60px;
    align-items: center;
}

.projects-card {
    position: relative;
    min-height:360px;
    width:60%;
    background-color: rgba(229, 231, 235, 0.1);
    border-radius: 10px;
    border: 1px solid rgba(229, 231, 235, 0.4);
    padding: 40px;
    transition: 0.3s;
    overflow:hidden;
}

.projects-card>div {
    display: flex;
    flex-direction: column;
    gap: 2px;
}

.projects-card>div:first-child {
    width: 45%;
    text-align: left;
}

.projects-card>div h5 {
    font-weight: bold;
    font-size: 20pt;
    @apply text-gray-800;
}

.projects-card>div p {
    line-height: 25px;
}

.projects-card>div>div {
    font-weight: bold;
    display: flex;
    align-items: center;
    gap: 12px;
    flex-wrap: wrap;
}

.projects-card>div:first-child>div img {
    height: 30px;
}

.projects-card:hover {
    background-color: rgba(229, 231, 235, 0.3);
}

.projects-card>div:last-child {
    position: absolute;
    height:0;
    bottom:0;
    right:0;
    padding-top: 28.125%;
    width: 50%;
}

.projects-card>div:last-child>img {
    position: absolute;
    top:-15%;
    left:0;
    height: 125%;
    width: 125%;
    max-width: 125%;
    border: 1px solid rgba(129, 131, 145, 0.4);
    border-radius: 5px;
    transition: 0.3s;
}

.projects-card:hover>div:last-child>img {
    transform: rotate(-4deg) translateX(-8%);    
}