.selfi {
    width: 60%;
    height: 0;
    padding-top: 60%;
    backdrop-filter: blur(5px);
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
    position: relative;
    overflow: hidden;
    border: 2px solid white;
    border-radius: 10px;
    margin: 20%;
}

.selfi>img {
    position:absolute;
    top: 0;
}