.career-sub-page {
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 30px;
    padding: 20px 0;
    align-items: center;
}

.career-item {
    width: 60%;
    text-align: left;
    display: flex;
}

.career-item h5 {
    @apply text-gray-800 text-lg font-bold;
}

.career-item p {
    line-height: 18px;
    margin-block: 4px;
    text-indent: -20px;
    margin-left: 20px;
}
.career-item p:first-child {
    margin-left: 0px;
    text-indent: 0px;
}

.career-item>div:first-child {
    width: 50px;
    display: flex;
    padding: 7px 10px;
    flex-direction: column;
    justify-content: stretch;
    align-items: center;
    gap: 20px;
}

.career-item>div:first-child>div:first-child {
    border-radius: 10px;
    width: 12px;
    height: 12px;
    border: solid 3.5px;
    @apply bg-gray-400 border-gray-300;
}

.career-item>div:first-child>div:last-child {
    flex-grow: 1;
    border: 1px solid;
    @apply border-gray-300;
    border-radius: 10px;
}