.project-card-comp {
    width: 100%;
    position: absolute;
}

.project-card-comp>div {
    position: relative;
    width: 100%;
    padding-top: 70%;
    transform: translateY(-50%);
    height:0;
    border: 2px solid white;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.2);
}

.project-card-comp>div>img {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
}