.home-sub-page {
    width: 100%;
    height: fit-content;
    display: flex;
    padding: 0% 10%;
    justify-content: stretch;
    display: flex;
    align-items: center;
    min-height: 80%;
    gap: 50px;
}

.home-sub-page>div:first-child {
    font-family: Quicksand,sans-serif!important;
    font-size: 20pt;
    width: 60%;
    text-align: left;
    padding: 0px 0px;
}

.home-sub-page>div:last-child {
    width: 40%;
    text-align: left;
    padding-top: 20%;
    position: relative;
}

@media (max-width: 1280px) {
    .home-sub-page {
        flex-direction: column;
    }
    .home-sub-page>div:first-child {
        width: 80%;
    }
    .home-sub-page>div:last-child {
        width: 60%;
        padding-top: 60%;
    }
}
@media (max-width: 1600px) {
    .home-sub-page {
        padding: 0 5%;
    }
}