.skills-sub-page {
    width: 100%;
    min-height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 60px;
}

.skill-item {
    width: fit-content;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.skill-item>img {
    height: 80px;
}

.skill-item>p {
    @apply text-gray-800;
}

.skills-sub-page>div:last-child {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 50px;
    padding: 0 10%;
}